import Dropdown from './dropdown';
import Icon from './icon';
import { h } from './element';
import { baseFormulas } from '../core/formula';
import { cssPrefix } from '../config';

export default class DropdownFormula extends Dropdown {
  constructor() {
    let nformulas = baseFormulas.slice(0);
    nformulas.splice(4, 0, { key: 'divider' });
    nformulas.splice(8, 0, { key: 'divider' });
    nformulas.splice(12, 0, { key: 'divider' });
    nformulas = nformulas.map((it) => {
      const item = h('div', `${cssPrefix}-item`);
      if (it.key === 'divider') {
        item.addClass('divider');
      } else {
        item
          .on('click', () => {
            this.hide();
            this.change(it);
          })
          .child(it.key);
      }
      return item;
    });
    super(new Icon('formula'), '180px', true, 'bottom-left', ...nformulas);
  }
}
