// Event Listeners

export const listenerAddSheet = (spreadsheetInstanceRef) => {
  socket.on("add-sheet", (data) => {
    // console.log('sheet-added');
    const { sheetIndex } = data;
    const ref = spreadsheetInstanceRef.current;
    // console.log('listener', sheetIndex);
    // console.log('current', ref.sheetIndex);
    if ((sheetIndex) !== ref.sheetIndex){
      const d = ref.addSheet();
      ref.sheet.resetData(d);
      // this.sheet.resetData(d);
    }

  });
};


export const listenerSingularCellSelection = (spreadsheetInstanceRef) => {
  socket.on("cell-selected", (data) => {
    const { columnIndex, rowIndex } = data;
    const { selector } = spreadsheetInstanceRef.current.sheet;
    selector.set(rowIndex, columnIndex);
  });
};

export const listenerMultipleCellSelection = () => {
  socket.on("cells-selected", (data) => {
    // console.log(data);
  });
};

export const listenerEditedCellData = (spreadsheetInstanceRef) => {
  socket.on("cell-edited", (data) => {
    const { dataText, rowIndex, columnIndex } = data;
    const ref = spreadsheetInstanceRef.current;
    const dataProxy = ref.data;
    const sIndex = ref.sheetIndex - 2;
    // console.log(data, "EDITED");
    const { rows } = dataProxy;

    // const cell = rows.getCell(rowIndex, columnIndex);
    // if ((cell && cStyle !== undefined) && (cell.style != cStyle)) {
    //   // console.log(cStyle,": daSTyles", cell, ": daCell", dataProxy, ": daDataProxy");
    //   cell.style = dataProxy.addStyle(cStyle);
    // }
    ref.cellText(rowIndex, columnIndex, dataText, sIndex).reRender();
    // console.log(sIndex);
  });
};

export const listenerCellStyle = (spreadsheetInstanceRef) => {
  socket.on("cell-style", (data) => {
    const { rowIndex, columnIndex, cStyle } = data;
    const ref = spreadsheetInstanceRef.current;
    const dataProxy = ref.data;
    const { rows } = dataProxy;

    const cell = rows.getCell(rowIndex, columnIndex);
    if (cell && cStyle !== undefined && cell.style !== cStyle) {
      // console.log(cStyle,": daSTyles", cell, ": daCell", dataProxy, ": daDataProxy");
      console.log(cStyle, "listenStyle");
      cell.style = dataProxy.addStyle(cStyle);
      ref.reRender();
    }
  });
};

