import { Avatar, Image } from "antd";
import { useState } from "react";
import iconImage from "../../../assets/iconImage.png";
import { DropDownMenu } from "./DropDownMenu/DropDownMenu";
import classes from "./SharePrivacy.module.css";

export const SharePrivacy = ({ contentToggle }) => {
  const [privacy, setPrivacy] = useState("Anyone");
  const [access,setAccess] = useState("Viewer");
  return (
    <div className={classes.ownerInfo}>
      <div className={classes.avatar}>
        <Avatar size="large" src={<Image src={iconImage} />} />
      </div>
      <div className={classes.info}>
        <div className={classes.name}>
          {contentToggle ? <h4>Name</h4> : <DropDownMenu menuItems={['Restricted', 'Anyone']} placement="bottomLeft" type={privacy} setType={setPrivacy} />}
        </div>
        <div className={classes.email}>
          {contentToggle ? (
            <p>email@gmail.com</p>
          ) : (privacy === "Anyone" ? (
            <p>Any one with the link can access</p>
          ) : (
            <p>only people added can open this link</p>
          ))}
        </div>
      </div>
      <div className={classes.badge}>
        {contentToggle ? <p>Owner</p> : privacy === "Anyone" ? <DropDownMenu menuItems={['viewer', 'commenter', 'Editor']} placement={"topLeft"} type={access} setType={setAccess} /> : ""}
      </div>
    </div>
  );
};
