import React, { useState } from 'react';
import MyNav from './Navigations.module.css';
import { NavTab } from '../NavTabs/NavTab';
import { navItems } from '../../constants/constants'

const Navigations = () => {

    const [dropDownToggle, setDropDownToggle] = useState(false);
    
    return (
        /*<h2 className={MyNav.navigationmain}>navContainer</h2>*/
        <div className={MyNav.navigationmain}>
            {navItems.map((item, ind) => <NavTab key={ind} setDropDownToggle={setDropDownToggle} dropDownToggle={dropDownToggle} title={item.navTitle} dropDownList={item.dropDownList} />)}
        </div>


    // return (
    //     <h2 className={MyNav.navigationmain}>test1</h2>

    );
}

export default Navigations;
