import React from "react";
import { useSpreadSheet } from "../../context/spreadsheet-context";
import {
  listenerSingularCellSelection,
  listenerMultipleCellSelection,
  listenerEditedCellData,
  listenerCellStyle,
  listenerAddSheet,
} from "../../socket";

export const SpreadsheetContainer = () => {
  const {
    spreadSheet_init,
    spreadsheetInstanceRef,
    onCellSelected,
    onCellsSelected,
    onCellEdited,
    onCellStyle,
    onAddSheet,
  } = useSpreadSheet();
  React.useEffect(() => {
    spreadSheet_init();
    onCellSelected();
    listenerSingularCellSelection(spreadsheetInstanceRef);
    onCellsSelected();
    listenerMultipleCellSelection();
    onCellEdited();
    listenerEditedCellData(spreadsheetInstanceRef);
    onCellStyle();
    listenerCellStyle(spreadsheetInstanceRef);
    onAddSheet();
    listenerAddSheet(spreadsheetInstanceRef);
  }, [
    onCellEdited,
    onCellsSelected,
    onCellSelected,
    onCellStyle,
    onAddSheet,
    spreadSheet_init,
    spreadsheetInstanceRef,
  ]);

  return <div id="spreadsheet-container"></div>;
};
